import React, { Component } from 'react'
import { connect } from 'react-redux'
import DataGrid from 'react-data-grid'
import 'react-data-grid/dist/react-data-grid.css';

import { getYAxisName } from '../utils/constants'
import './DataTable.css'

const mapStateToProps = state => {
  return {
    country: state.country
  }
}

class DataTable extends Component {
  constructor(props){
    super(props)
    this.state = {
    }
  }

  fillData = (country, data, mode) => {
    let filledData = {}
    let columns = [{ key: 'country', name: 'Country' }]
    let rows = [{ country: country.label }]
    data.map((entry, index) => {
      columns.push({ key: data[index].date, name: data[index].date })
      rows[0][data[index].date] = data[index][getYAxisName(mode)]
    })
    filledData["columns"] = columns
    filledData["rows"] = rows
    return filledData
  }

  render() {
    const { country, mode, data } = this.props
    const filledData = this.fillData(country, data, mode)

    return (
      <DataGrid
        columns={filledData.columns}
        rows={filledData.rows}
      />
    )
  }
}

export default connect(mapStateToProps)(DataTable)
