import moment from 'moment'

export const getData = (mode, countryData, dayValueOfMovingAverage, startDate, endDate) => {
  let data = {}
  let xyData = []
  let dataMaxX = ''
  let dataMaxY = 0
  const countryDataArray = Object.entries(countryData)

  if(mode === 'DAILY_CASE'){
    countryDataArray.map((dailyData, index) => {

      // Calculate only selected interval
      if(moment(dailyData[0]).isBetween(startDate, endDate)){
        let totalCases = 0
        let newCountryDataArray = []

        // Get relevant array for calculation according to day interval for moving average. If no data available then start from the first available day.
        if(null != countryDataArray[index-dayValueOfMovingAverage]){
          newCountryDataArray = countryDataArray.slice(index-dayValueOfMovingAverage, index)
        }
        else{
          newCountryDataArray = countryDataArray.slice(0, index)
        }

        // Total daily cases in selected interval
        newCountryDataArray.map(newDailyData => {
          totalCases += newDailyData[1]["new_daily_cases"]
        })

        // Finding and setting the moving average
        let movingAverage = totalCases / dayValueOfMovingAverage
        xyData.push({ date: moment(dailyData[0]).format("DD/M/YY"), average: Math.floor(movingAverage  * 100) / 100 })

        // Finding and setting the peak point(s)
        if(movingAverage > dataMaxY){
          dataMaxX = moment(dailyData[0]).format("DD/M/YY")
          dataMaxY = movingAverage
        }
      }
    })
  }
  else if(mode === 'MORTALITY_RATE_PERCENTAGE'){
    countryDataArray.map((dailyData, index) => {

      if(moment(dailyData[0]).isBetween(startDate, endDate)){
        let totalPercentage = 0
        let newCountryDataArray = []

        // Get relevant array for calculation according to day interval for moving average. If no data available then start from the first available day.
        if(null != countryDataArray[index-dayValueOfMovingAverage]){
          newCountryDataArray = countryDataArray.slice(index-dayValueOfMovingAverage, index)
        }
        else{
          newCountryDataArray = countryDataArray.slice(0, index)
        }

        // Total death percentage(total deaths / total cases) in selected interval
        newCountryDataArray.map(newDailyData => {
          totalPercentage += newDailyData[1]["total_deaths"] / newDailyData[1]["total_cases"]
        })

        // Finding and setting the moving average
        let movingAverage = totalPercentage / dayValueOfMovingAverage
        xyData.push({ date: moment(dailyData[0]).format("DD/M/YY"), average: Math.floor(movingAverage  * 10000) / 10000 })

        // Finding and setting the peak point(s)
        if(movingAverage > dataMaxY){
          dataMaxX = moment(dailyData[0]).format("DD/M/YY")
          dataMaxY = movingAverage
        }
      }
    })
  }
  else if(mode === 'DAILY_DEATH_CHANGE'){
    countryDataArray.map((dailyData, index) => {

      if(moment(dailyData[0]).isBetween(startDate, endDate)){
        let dailydeathchange = 0

        // Get relevant array for calculation
        if(null != countryDataArray[index-dayValueOfMovingAverage]){
          dailydeathchange = countryDataArray[index][1]["new_daily_deaths"] - countryDataArray[index-dayValueOfMovingAverage][1]["new_daily_deaths"]
        }

        // Finding and setting the daily death change
        xyData.push({ date: moment(dailyData[0]).format("DD/M/YY"), dailydeathchange: dailydeathchange })
      }
    })
  }

  data["peakData"] = { dataMaxX: dataMaxX, dataMaxY: dataMaxY }
  data["xyData"] = xyData
  return data
}
