export const API_DIRECTORY_COUNTRY = 'https://api.thevirustracker.com/free-api?countryTimeline='

export const getMovingAverageDayInterval = (mode) => {
  let dayInterval
  switch (mode) {
      case 'DAILY_CASE':
          dayInterval = 7
          break
      case 'MORTALITY_RATE_PERCENTAGE':
          dayInterval = 3
          break
      default:
          dayInterval = 1
  }
  return dayInterval
}

export const getYAxisName = (mode) => {
  let yAxisName
  switch (mode) {
      case 'DAILY_CASE':
          yAxisName = 'average'
          break
      case 'MORTALITY_RATE_PERCENTAGE':
          yAxisName = 'average'
          break
      case 'DAILY_DEATH_CHANGE':
          yAxisName = 'dailydeathchange'
          break
      default:
          yAxisName = 'y'
  }
  return yAxisName
}
