import React, { Component } from 'react'
import moment from 'moment'
import { BarChart, Bar, Cell, XAxis, YAxis, Tooltip, ResponsiveContainer, ReferenceLine } from 'recharts'
import DatePicker from "react-datepicker"

import DataTable from '../components/DataTable'
import { getMovingAverageDayInterval } from '../utils/constants'
import { getData } from '../utils/covidCalculations'
import './ChartBar.css'

class ChartBar extends Component {
  constructor(props){
    super(props)
    this.state = {
      dayValueOfMovingAverage: getMovingAverageDayInterval(this.props.mode),
      startDate: moment().subtract(60, "days").format("M/DD/YY"),
      endDate: moment().format("M/DD/YY"),
      isDataTableHidden: true
    }
  }

  handleDataTable = () => {
    // Toggle datatable according to previous state
    this.setState(prevState => ({
      ...prevState,
      isDataTableHidden: !prevState.isDataTableHidden
    }))
  }

  handleStartDateChange = (date) => {
    this.setState({ startDate: moment(date).format("M/DD/YY") })
  }

  handleEndDateChange = (date) => {
    this.setState({ endDate: moment(date).format("M/DD/YY") })
  }

  render() {
    const { mode, countryData } = this.props
    if(null == countryData){
      return null
    }
    const { dayValueOfMovingAverage, startDate, endDate, isDataTableHidden } = this.state
    let name
    switch (mode) {
        case 'DAILY_DEATH_CHANGE':
            name = 'Daily new deaths changed from the previous day’s death count'
            break
        default:
            name = ''
    }

    //Getting data from props according to calculations
    const data = getData(mode, countryData, dayValueOfMovingAverage, moment(startDate).subtract(1, "days").format("M/DD/YY"), moment(endDate).add(1, "days").format("M/DD/YY"))

    const dataTableArea =
    isDataTableHidden
    ?
    null
    :
    <DataTable data={data.xyData} mode={mode} />

    return (
      <>
      <div className="barchart-container">
      <div className="barchart-name">
        {name}
      </div>
      <div className="barchart-wrapper">
      <ResponsiveContainer width="100%" height={350} display="inline-block">
      <BarChart data={data.xyData} margin={{ top: 5, right: 5, left: 0, bottom: 5 }}>
        <Bar dataKey="dailydeathchange" fill="#8884d8" >
        {
           data.xyData.map((entry, index) => (
             <Cell key={`cell-${index}`} fill={entry.dailydeathchange > 0 ? "#D62728" : "#2E8B57"}/>
           ))
         }
        </Bar>
        <XAxis dataKey="date" interval="preserveEnd" tick={{ angle: -10 }} />
        <YAxis type="number" domain={['dataMin', dataMax => dataMax < 100 ? Math.floor(dataMax  * 100) / 100 : Math.ceil(Math.trunc(dataMax)/100)*100]}  >
        </YAxis>
        <ReferenceLine y={0} stroke="#000" />
        <Tooltip />
      </BarChart>
      </ResponsiveContainer>
      </div>
      </div>
      <div className="barchart-dates-container">
      <div className="barchart-date-container barchart-startdate">
      <span className="barchart-date-label">Start date</span>
      <DatePicker
        className="barchart-startdatepicker"
        dateFormat="dd/M/yy"
        selected={new Date(startDate)}
        maxDate={new Date(endDate)}
        onChange={this.handleStartDateChange}
      />
      </div>
        <div className="barchart-date-container barchart-enddate">
        <span className="barchart-date-label">End date</span>
        <DatePicker
          className="barchart-enddatepicker"
          dateFormat="dd/M/yy"
          selected={new Date(endDate)}
          minDate={new Date(startDate)}
          maxDate={new Date()}
          onChange={this.handleEndDateChange}
        />
        </div>
      </div>
      <div className="barchart-datatable-container">
        <div className="barchart-datatablebutton" onClick={this.handleDataTable}>
          <div className="barchart-datatablevalue">{isDataTableHidden ? "Show" : "Hide"} datatable</div>
        </div>
        {dataTableArea}
      </div>
      </>
    )
  }
}

export default ChartBar
