import React, { Component } from 'react'
import moment from 'moment'
import { LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip, ResponsiveContainer, Label, ReferenceDot } from 'recharts'
import DatePicker from "react-datepicker"
import "react-datepicker/dist/react-datepicker.css"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronDown } from "@fortawesome/free-solid-svg-icons"

import DataTable from '../components/DataTable'
import { getMovingAverageDayInterval } from '../utils/constants'
import { getData } from '../utils/covidCalculations'
import './ChartLine.css'

class ChartLine extends Component {
  constructor(props){
    super(props)
    this.state = {
      dayValueOfMovingAverage: getMovingAverageDayInterval(this.props.mode),
      startDate: moment().subtract(60, "days").format("M/DD/YY"),
      endDate: moment().format("M/DD/YY"),
      isDayIntervalDropdownHidden: true,
      isDataTableHidden: true
    }
  }

  componentDidMount(){
    document.addEventListener('click', this.handleClickOutsideDayButton, true)
    document.addEventListener('touchstart', this.handleClickOutsideDayButton, true)
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.handleClickOutsideDayButton, true)
    document.removeEventListener('touchstart', this.handleClickOutsideDayButton, true)
  }

  handleClickOutsideDayButton = (e) => {
    // Toggle day dropdown down when clicked outside
    const dayDropdown = document.getElementById("linechart-daydropdown")
    const dayButton = document.getElementById("linechart-daybutton")
    if (null !== dayDropdown && !dayDropdown.contains(e.target) && null !== dayButton && !dayButton.contains(e.target)) {
      this.setState({ isDayIntervalDropdownHidden: true })
    }
  }

  handleDayIntervalDropdown = () => {
    // Toggle day dropdown according to previous state
    this.setState(prevState => ({
      ...prevState,
      isDayIntervalDropdownHidden: !prevState.isDayIntervalDropdownHidden
    }))
  }

  handleDataTable = () => {
    // Toggle datatable according to previous state
    this.setState(prevState => ({
      ...prevState,
      isDataTableHidden: !prevState.isDataTableHidden
    }))
  }

  changeDayInterval = (e) => {
    const selectedDay = e.currentTarget
    const dayinterval = selectedDay.dataset.dayinterval
    this.setState({ dayValueOfMovingAverage: dayinterval, isDayIntervalDropdownHidden: true })
  }

  handleStartDateChange = (date) => {
    this.setState({ startDate: moment(date).format("M/DD/YY") })
  }

  handleEndDateChange = (date) => {
    this.setState({ endDate: moment(date).format("M/DD/YY") })
  }

  render() {
    const { mode, countryData } = this.props
    if(null == countryData){ return null }
    const { dayValueOfMovingAverage, startDate, endDate, isDataTableHidden } = this.state
    let name
    switch (mode) {
        case 'DAILY_CASE':
            name = 'moving average of daily cases'
            break
        case 'MORTALITY_RATE_PERCENTAGE':
            name = 'moving average of mortality rate'
            break
        default:
            name = ''
    }

    //Getting data from props according to calculations
    const data = getData(mode, countryData, dayValueOfMovingAverage, moment(startDate).subtract(1, "days").format("M/DD/YY"), moment(endDate).add(1, "days").format("M/DD/YY"))

    const dayArea =
    <>
    <div id="linechart-daybutton" className="linechart-daybutton" onClick={this.handleDayIntervalDropdown}>
      <div className="linechart-daybutton-wrapper">
      <span className="linechart-dayvalue-wrapper">{dayValueOfMovingAverage}-day</span>
      <span className="linechart-dayicon-wrapper"><FontAwesomeIcon icon={faChevronDown} color="#96ADC8"/></span>
      </div>
    </div>
    <div id="linechart-daydropdown" className="linechart-daydropdown" style={{display: this.state.isDayIntervalDropdownHidden ? 'none' : 'inline-block'}}>
      {
        [...Array(16)].map((x, i) => {
          if(i > 1){
            return <div className="linechart-dayselect" data-dayinterval={i} key={i+"day"} onClick={this.changeDayInterval}>
              {i}-day
            </div>
          }
        })
      }
    </div>
    </>

    const dataTableArea =
    isDataTableHidden
    ?
    null
    :
    <DataTable data={data.xyData} mode={mode} />

    return (
      <>
      <div className="linechart-container">
      <div className="linechart-name-container">
        {dayArea}
        <span className="linechart-name">{name}</span>
      </div>
      <div className="linechart-wrapper">
      <ResponsiveContainer width="100%" height={350} display="inline-block">
      <LineChart data={data.xyData} margin={{ top: 5, right: 5, left: 0, bottom: 5 }}>
        <Line type="monotone" dataKey="average" stroke="#d88484" isAnimationActive={false} dot={false} />
        <CartesianGrid stroke="#ccc" strokeDasharray="3 3" />
        <XAxis dataKey="date" interval="preserveEnd" tick={{ angle: -10 }} />
        <YAxis type="number" interval="preserveStartEnd" domain={[0, dataMax => dataMax * 1.2 < 10 ? Math.floor(dataMax * 1.2  * 100) / 100 : Math.ceil(Math.trunc(dataMax * 1.2)/100)*100]} >
        </YAxis>
        <Tooltip />
        <ReferenceDot isFront={true} r={5} x={data.peakData.dataMaxX} y={data.peakData.dataMaxY} fill="#D62728" stroke="none">
          <Label value="Peak" offset={0} position="top" />
        </ReferenceDot>
      </LineChart>
      </ResponsiveContainer>
      </div>
      </div>
      <div className="linechart-dates-container">
        <div className="linechart-date-container linechart-startdate-container">
        <span className="linechart-date-label">Start date</span>
        <DatePicker
          className="linechart-startdatepicker"
          dateFormat="dd/M/yy"
          selected={new Date(startDate)}
          maxDate={new Date(endDate)}
          onChange={this.handleStartDateChange}
        />
        </div>
        <div className="linechart-date-container linechart-enddate-container">
        <span className="linechart-date-label">End date</span>
        <DatePicker
          className="linechart-enddatepicker"
          dateFormat="dd/M/yy"
          selected={new Date(endDate)}
          minDate={new Date(startDate)}
          maxDate={new Date()}
          onChange={this.handleEndDateChange}
        />
        </div>
      </div>
      <div className="linechart-datatable-container">
      <div className="linechart-datatablebutton" onClick={this.handleDataTable}>
        <div className="linechart-datatablevalue">{isDataTableHidden ? "Show" : "Hide"} datatable</div>
      </div>
      {dataTableArea}
      </div>
      </>
    )
  }
}

export default ChartLine
