import {
  SET_COUNTRY
} from '../actions/countryActions'

const initialCountryState = {
	value: "",
  label: ""
}

export default function reducer (state = initialCountryState, action){
	switch(action.type){
		case SET_COUNTRY: {
			return {
				...state,
				value: action.country.value,
        label: action.country.label
			}
		}
		default:
      return state
	}
}
