import React, { Component } from 'react'
import { connect } from 'react-redux'

import ChartLine from '../../components/ChartLine'
import ChartBar from '../../components/ChartBar'
import { setCountry } from "../../state/actions/countryActions"
import { COUNTRIES } from '../../utils/countryList'
import { API_DIRECTORY_COUNTRY } from '../../utils/constants'
import './App.css'

const mapStateToProps = state => {
  return {
    country: state.country
  }
}

const actionCreators = {
  setCountry
}

class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      datadownloading: false,
    }
  }

  componentDidUpdate(prevProps){
    const { country } = this.props
    if(country !== prevProps.country){
      this.getCountryData()
    }
  }

  getCountryData = async () => {
    const { country } = this.props
    this.setState({ datadownloading: true })

    const res = await
    fetch(API_DIRECTORY_COUNTRY + country.value)
    // API JSON objesi döndürmek yerine uyarı olursa gömülü şekilde html etiketlerini de döndürüyor. O nedenle text'i önce temizleyip sonra JSON'a parse etmeyi tercih ettim.
    let resCountryDataText = await res.text()
    resCountryDataText = resCountryDataText.split('<br />')
    const resCountryDataJSON = JSON.parse(resCountryDataText[resCountryDataText.length - 1])
    this.setState({ datadownloading: false })
    if(null != resCountryDataJSON.timelineitems && resCountryDataJSON.timelineitems[0].stat === "ok"){
      this.setState({ countryData: resCountryDataJSON.timelineitems[0] })
    }
    else{
      this.setState({ countryData: "" })
    }
  }

  handleChangeCountry = () => {
    let countrySelect = document.getElementById("country-select")
    let selectedCountry = countrySelect.options[countrySelect.selectedIndex]
    const currentCountry = { value: selectedCountry.value, label: selectedCountry.text }
    this.props.setCountry(currentCountry)
  }

  render() {
    const { datadownloading, countryData } = this.state
    const chartsArea = datadownloading
    ?
    <div className="charts-container charts-container-downloading">
      <div className="charts-container-downloading-text">
        Loading...
      </div>
    </div>
    :
    (
    countryData === ""
    ?
    <div className="charts-container">
      <div className="charts-container-text">
        The country you select have no data available right now.
      </div>
    </div>
    :

      <div className="charts-container">
        <ChartLine
          mode = 'DAILY_CASE'
          countryData = {this.state.countryData}
        />
        <ChartLine
          mode = 'MORTALITY_RATE_PERCENTAGE'
          countryData = {this.state.countryData}
        />
        <ChartBar
          mode = 'DAILY_DEATH_CHANGE'
          countryData = {this.state.countryData}
        />
      </div>
    )
    return (
      <div className="app-container">
        <h1>COVID-19 Country Data Visualization</h1>
        <div className="country-select-container">
        <select id="country-select" className="country-select" onChange={this.handleChangeCountry}>
          <option value="" hidden>Select a country</option>
          {
            null != COUNTRIES && COUNTRIES.map((Country)=>{
              return <option key={Country.value} value={Country.value}>{Country.label}</option>
            })
          }
        </select>
        </div>
        {chartsArea}
      </div>
    )
  }
}

export default connect(mapStateToProps, actionCreators)(App)
